import React, { Component } from 'react';
import { connect } from 'react-redux';
import { login, loadUser } from '../store/actions/user.actions';
import { saveContact, getNeighbors } from '../store/actions/contacts.actions';
import { contactService } from '../services/contact.service';
class _TransferFund extends Component {
  state = {
    amount: '',
    recharge: false,
  };
  async componentDidMount() {
    await this.props.loadUser();
  }

  animateButton = (e) => {
    //reset animation
    // e.preventDefault();

    e.target.classList.remove('animate');

    e.target.classList.add('animate');
    setTimeout(function () {
      e.target.classList.remove('animate');
    }, 700);
  };

  transfer = async (ev) => {
    ev.preventDefault();
    const amount = this.state.amount;
    const { coins, moves } = this.props.user.info;
    if (amount > coins) {
      this.setState({ recharge: true, amount: '' });
      return;
    } else if (!amount) {
      console.log('ya poor');
    } else {
      const move = this.getMove(amount);
      const user = {
        ...this.props.user,
        info: { coins: coins - amount, moves: [move, ...moves] },
      };
      const contact = {
        ...this.props.contact,
        info: {
          coins: this.props.contact.info.coins + amount,
          moves: [move, ...this.props.contact.info.moves],
        },
      };

      await this.props.saveContact(contact);
      await this.props.saveContact(user);
      await this.props.login(user);
      await this.props.getNeighbors(contact._id);
      this.setState({ amount: '' });
      this.props.goBack();
    }
  };

  recharge = async (ev) => {
    ev.preventDefault();

    const amount = this.state.amount;
    if (!amount) return;
    const { coins, moves } = this.props.user.info;
    const move = this.getMoveRecharge(amount);

    const user = {
      ...this.props.user,
      info: { coins: coins + amount, moves: [move, ...moves] },
    };

    await this.props.saveContact(user);
    await this.props.login(user);
    this.setState({ recharge: false, amount: '' });
  };

  toggleRecharge = () => {
    this.setState(({ recharge }) => {
      this.setState({ recharge: !recharge, amount: '' });
    });
  };

  getMove(amount) {
    return {
      type: 'transfer',
      from: this.props.user.name,
      to: this.props.contact.name,
      amount,
      _id: contactService.makeId(10),
    };
  }
  getMoveRecharge(amount) {
    return {
      type: 'recharge',
      from: this.props.user.name,
      amount,
      _id: contactService.makeId(10),
    };
  }

  handleChange = ({ target }) => {
    let value = +target.value || '';
    this.setState({ amount: value });
  };
  render() {
    const contact = this.props.contact;
    const { amount } = this.state;
    const { recharge } = this.state;
    return (
      <div className='transfer-funds'>
        {recharge && (
          <div className='recharge'>
            <h2>Recharge</h2>
            <form onSubmit={this.recharge}>
              <label>
                <span>Amount : </span>
                <input
                  type='number'
                  name='amount'
                  value={amount}
                  onChange={this.handleChange}
                  min='0'
                />
              </label>
              <button onClick={this.animateButton} className='bubbly-button'>Recharge!</button>
            </form>{' '}
            <button className='toggle-transfer' onClick={this.toggleRecharge}>
              Transfer
            </button>
          </div>
        )}
        {!recharge && (
          <>
            <h2>Transfer coins to {contact.name}:</h2>
            <form onSubmit={this.transfer}>
              <label>
                <span>Amount : </span>
                <input
                  type='number'
                  name='amount'
                  value={amount}
                  onChange={this.handleChange}
                  min='0'
                />
              </label>
              <button onClick={this.animateButton} className='bubbly-button'>Transfer!</button>
            </form>
            <button className='toggle-transfer' onClick={this.toggleRecharge}>
              Recharge
            </button>
          </>
        )}
        <h3>Your current balance is {this.props.user.info.coins}</h3>
      </div>
    );
  }
}

const mapStateToProps = (state) => ({
  user: state.userModule.loggedInUser,
});

const mapDispatchToProps = {
  login,
  loadUser,
  saveContact,
  getNeighbors,
};

export const TransferFund = connect(
  mapStateToProps,
  mapDispatchToProps
)(_TransferFund);
