import React, { Component } from 'react';
import { Chart } from '../cmps/Chart';
import { CarouselFade } from '../cmps/Carousel';
import { bitcoinService } from '../services/bitcoin.service';
import {
  CarouselProvider,
  Slider,
  Slide,
  ButtonBack,
  ButtonNext,
} from 'pure-react-carousel';
import 'pure-react-carousel/dist/react-carousel.es.css';

export class statistics extends Component {
  state = {
    chartA: null,
    chartB: null,
  };
  componentDidMount() {
    this.loadChart();
  }

  loadChart = async () => {
    try {
      const chartA = await bitcoinService.getChartA();
      const chartB = await bitcoinService.getChartB();
      this.setState({ chartA, chartB });
    } catch (err) {
      console.log('had a big poopoo :', err);
    }
  };
  render() {
    const { chartA, chartB } = this.state;
    if (!chartA || !chartB) return <div className='loader'>loading...</div>;

    return (
      <div className='statistics'>
        <Chart data={chartA} />
        <Chart data={chartB} />
      </div>
    );
  }
}
